<template>
	<el-card>
		<div slot="header">
			<span>编辑公告</span>
		</div>
		<el-form ref="form" :rules="rules" :model="form" status-icon label-width="100px">
			<el-form-item prop="title" label="标题">
				<el-input v-model="form.title"></el-input>
			</el-form-item>
			<el-form-item prop="is_sticky" label="置顶">
				<el-radio-group v-model="form.is_sticky">
					<el-radio :label="1">是</el-radio>
					<el-radio :label="0">否</el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item prop="content" label="内容">
				<Editor :mode="mode" v-model="form.content"></Editor>
			</el-form-item>
			<el-form-item>
				<el-button @click="handleEdit" type="primary">保存修改</el-button>
			</el-form-item>
		</el-form>
	</el-card>
</template>

<script>
import { Notice } from '@/api';
import Editor from '@/components/txt-editor.vue';

export default {
	components: {
		Editor,
	},
	data() {
		return {
			mode: 'simple',
			form: {
				title: '',
				is_sticky: '',
				content: '',
			},
			rules: {
				title: [{ required: true, message: "请填写公告标题", trigger: 'blur' }],
				is_sticky: [{ required: true, message: "请选择是否置顶", trigger: 'blur' }],
				content: [{ required: true, message: "请输入公告内容", trigger: 'blur' }],
			}
		}
	},
	created() {
		// 获取参数
		let { notice_id } = this.$route.params;
		// 初始化
		this.init(notice_id);
	},
	methods: {
		// 初始化
		async init(notice_id) {
			// 加载公告列表
			let { status, msg, data } = await Notice.detail({ notice_id });
			if (status) {
				// 还原表单
				this.form = data;
			}
		},
		// 保存
		async handleEdit() {
			let valid = await this.$refs.form.validate();
			if (valid) {
				let { status, msg } = await Notice.edit({ ...this.form });
				if (status) {
					this.$message.success(msg);
					this.$router.go(-1);
				} else {
					this.$message.error(msg);
				}
			}
		},
	}
}
</script>

